footer{
    display: flex;
    flex-direction: column;
    z-index: 99;
    .footer{
        background: #232930;
        display: flex;
        align-items: stretch;
        justify-content: space-between;
        padding: 50px 100px;
        .footer-item{
            display: flex;
            color: #ffffff;
            flex-direction: column;
            gap: 10px;
            img{
                width: 172px;
                height: auto;
            }
            a{
                color: #aeabab;
                font-size: 14px;
                text-decoration: none;
                line-height: 140%;
                &.social-links{
                    display: flex;
                    gap: 4px;
                    align-items: center;
                    img{
                        width: 12px;
                        height: 12px;
                        filter: invert(.69);
                    }
                }
                &:hover{
                    color: #ffffff;
                    transition: 0.3s ease-in-out;
                    &.social-links img{
                        filter: invert(1);
                    }
                }
                &.active{
                    color: #E8E8E8;
                   text-decoration: underline;
                }
            }
            p{
                color: #ffffff;
                font-size: 14px;
            }
        }
    }
    .copyRight{
        display: flex;
        align-items: center;
        justify-content: center;
        padding: 10px;
        background: #2c3238;
        p{
            font-size: 14px;
            color: #aeabab;
        }
    }
}

@media screen and (max-width:970px) {
    footer .footer{
        justify-content: center;
        align-items: center;
        flex-direction: column;
        flex-wrap: wrap;
        padding: 24px 16px;
        gap: 20px;
        .footer-item{
            justify-content: center;
            align-items: center;
            p{
                text-align: center;
            }
        }
    }
}

@media screen and (min-width:768px) and (max-width: 1024px){
    footer .footer{
        padding: 50px;
    }

}