* {
  font-family: "Inter", sans-serif;
  margin: 0;
  padding: 0;
}
input,
textarea {
  appearance: none; /* Standard */
  -webkit-appearance: none; /* Chrome, Safari, iOS */
  -moz-appearance: none; /* Firefox */
}

body {
  font-family: "Inter", sans-serif;
  margin: 0;
  padding: 0;
}
.theme-text {
  color: #091678 !important;
}

.container {
  width: 100%;
  max-width: 1240px;
  margin: auto;
  display: flex;
  flex-direction: column;
  align-items: center;
  box-sizing: border-box;
}
.strech-content {
  margin: unset;
  width: 100%;
  max-width: unset;
}

.flex {
  display: flex;
  flex-wrap: wrap;
}

.flex-col {
  display: flex;
  flex-direction: column;
}
.nowrap{
  flex-wrap: nowrap;
}
.j-between{
  justify-content: space-between;
}

.align-center {
  align-items: center;
}
.align-start {
  align-items: flex-start;
}

/* gaps  */

.gap-12 {
  gap: 12px;
}
.gap-16 {
  gap: 16px;
}
.gap-20 {
  gap: 20px;
}
.gap-30 {
  gap: 20px;
}
.gap-40 {
  gap: 40px;
}
.gap-50 {
  gap: 50px;
}
.gap-80 {
  gap: 80px;
}

/* padding */
.py-40 {
  padding-top: 40px;
  padding-bottom: 40px;
}
.py-80 {
  padding-top: 80px;
  padding-bottom: 80px;
}

.m-0{
  margin: 0 !important;
}

/* typography  */

.heading3 {
  color: #383838;
  font-size: 38px;
  font-weight: 600;
  line-height: 140%;
}

.heading6 {
  color: #383838;
  font-size: 18px;
  font-weight: 600;
  line-height: 140%;
}

.subtitle {
  font-size: 22px;
  font-weight: 600;
  line-height: 140%;
}

.text {
  font-size: 16px;
  font-weight: 400;
  line-height: 140%;
  color: #383838;
}
.sub-title {
  font-size: 16px;
  font-weight: 400;
  line-height: 140%;
  color: #727272;
}
.text-center {
  text-align: center;
}
.text-green{
  color: #008000;
}
.text-left {
  text-align: left;
}
.flex-row{
  flex-direction: row;
}
.f-20 {
  font-size: 20px;
}

.f-18 {
  font-size: 18px;
}

.f-14 {
  font-size: 14px;
}

.bold {
  font-weight: 600;
}
.capitalize{
  text-transform: capitalize;
}
.uppercase{
  text-transform: uppercase;
}

.cta-btn {
  background: #091678;
  border: 1px solid transparent;
  color: #ffffff;
  font-size: 14px;
  font-weight: 500;
  display: flex;
  align-items: center;
  justify-content: center;
  white-space: nowrap;
  border-radius: 12px;
  height: 40px;
  width: 122px;
  cursor: pointer;
  padding: 20px;
  box-sizing: border-box;
  transition: all 0.3s ease-in-out;
  img{
    margin-left: 5px;
    transition: filter 0.3s ease-in-out;
  }
  &:hover.btn-white{
    color: #fff;
    background: #00000000;
    border-color:#fff ;
    img{
      filter: grayscale(1) invert(1);
    }
  }
   &:not(.btn-white){
    img{
      filter: grayscale(1) invert(1);
    }
    &:hover{
      color: #091678;
      background: #ffffff;
      border: 1px solid #091678;
      img{
        filter: unset;
      }
    }

  }
 
}

.radius-full {
  border-radius: 99px;
}
.for_mobile {
  display: none !important;
}

@media screen and (max-width: 767px) {
  .for_mobile {
    display: flex !important;
  }

  .for_desktop {
    display: none !important;
  }
  .heading6 {
    font-size: 16px;
  }
  .text {
    font-size: 14px;
  }
  .sub-title {
    font-size: 14px;
  }
  .cta-btn {
    padding: 8px 20px;
  }
  .f-18 {
    font-size: 18px;
  }
  .gap-80 {
    gap: 40px;
  }
  .gap-50 {
    gap: 25px;
  }
  .py-40 {
    padding-top: 20px;
    padding-bottom: 20px;
  }
}
